import { React, useState } from 'react'
import Button from '@mui/material/Button'
import Modal from '@mui/material/Modal'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import MenuItem from '@mui/material/MenuItem'
import Autocomplete from '@mui/material/Autocomplete'
import Select from '@mui/material/Select'
import Stack from '@mui/material/Stack'
import SendIcon from '@mui/icons-material/Send'
import './FindPW.css';

const Cancel = () => {
    const [open, setOpen] = useState(false)
    const handleOpen = () => setOpen(true)
    const handleClose = () => setOpen(false)

    return (
        <div>
            <Button onClick={handleOpen}></Button>
            <Modal
                open={open}
                onClose={handleClose}
            >
                <Box className="modal">
                    <Button onClick={handleClose}></Button>
                    <Button onClick={handleClose}></Button>
                </Box>
            </Modal>
        </div>
    )
}

const FindPW = () => {
    const EMAILADDRESS = ['naver.com', 'gmail.com', 'dgu.ac.kr', 'daum.net', 'hanmail.com', 'nate.com']

    const [open, setOpen] = useState(false)
    const [email, setEmail] = useState('')
    const [emailAddress, setEmailAddress] = useState('')
    const [inputSecurityCode, setInputSecurityCode] = useState('')
    const [securityCode, setSecurityCode] = useState('')
    const [password, setPassword] = useState('')
    const [passwordCheck, setPasswordCheck] = useState('')
    const [emailCheck, setEmailCheck] = useState(false)
    const [securityCheck, setSecurityCheck] = useState(false)
    const [emptyEmail, setEmptyEmail] = useState(false)
    const [emptyEmailAddress, setEmptyEamilAddress] = useState(false)
    const [emptyPW, setEmptyPW] = useState(false)
    const [incorrectPW, setCorrectPW] = useState(false)
    const [incorrectSecureCode, setIncorrectSecureCode] = useState(false)

    const handleOpen = () => setOpen(true)
    const handleClose = () => setOpen(false)

    const onChangeEmail = (e) => { setEmail(e.target.value) }

    const onChangeEmailAddress = (e) => {
        setEmailAddress(e.target.value)
        console.log(emailAddress)
    }

    const onChangeSecurityCode = (e) => {
        setInputSecurityCode(e.target.value)
    }

    const onChangePassword = (e) => {
        setPassword(e.target.value)
    }

    const onChangePasswordCheck = (e) => {
        setPasswordCheck(e.target.value)
    }

    const handleClickChangePassword = () => {
        const data = {
            pw: password,
        }

        if (password === '') {
            console.log('  ')
            setEmptyPW(true)
        }
        else {
            console.log(' ')
            setEmptyPW(false)
        }
        if (password === passwordCheck) {
            console.log(' ')
            setCorrectPW(false)
            //  
            fetch("/changepw", {
                method: 'post',
                headers: {
                    "content-type": "application/json",
                },
                body: JSON.stringify(data),
            })
        }
        else {
            console.log(' ')
            setCorrectPW(true)
        }
    }

    const handleClickSend = (e) => {
        console.log(email + '@' + emailAddress)

        if (email === '') {
            if (emailAddress === '') {
                //   X 
                setEmptyEamilAddress(true)
                setEmptyEmail(true)
            }
            else {
                //    
                setEmailCheck(false)
            }
        }
        else {
            if (emailAddress === '') {
                //   
                setEmailAddress(true)
            }
            else {
                //    
                setEmptyEmail(false)
                setEmptyEamilAddress(false)
                setEmailCheck(true)

                const data = {
                    email: email + '@' + emailAddress,
                }

                //    
                fetch("/sendemail", {
                    method: 'post',
                    headers: {
                        "content-type": "application/json",
                    },
                    body: JSON.stringify(data),
                })
                    .then((res) => res.json())
                    .then((json) => {
                        setSecurityCode(json.number)
                    })
            }
        }
    }

    const handleClickResend = () => {
        const data = {
            email: email,
        }

        //   
        fetch("/sendemail", {
            method: 'post',
            headers: {
                "content-type": "application/json",
            },
            body: JSON.stringify(data),
        })
            .then((res) => res.json())
            .then((json) => {
                setSecurityCode(json.number)
            })
    }

    const handleClickSecureCodeCheck = () => {
        //  
        if (securityCode === inputSecurityCode) {
            setSecurityCheck(true)
        }
        //  
        else {
            alert('  .')
        }

    }

    return (
        <div>
            <button className="sub_btn" onClick={handleOpen}> </button>
            <Modal
                open={open}
                onClose={handleClose}
            >
                <Box className="modal">
                    <Box className="findPW">
                        
                    </Box>
                    <Stack direction={{ xs: 'column', sm: 'row' }} alignItems="center" spacing={{ xs: 0, sm: 4 }}>
                        <Stack direction="row" alignItems="center" spacing={1}>
                            <TextField
                                disabled={emailCheck}
                                id='email'
                                type='text'
                                error={emptyEmail}
                                helperText={emptyEmail && ' .'}
                                value={email}
                                label=''
                                variant='outlined'
                                size="small"
                                margin='normal'
                                onChange={onChangeEmail} />
                            <span>@</span>
                            <Select
                                className="select"
                                error={emptyEmailAddress}
                                labelId="emailAddress"
                                value={emailAddress}
                                name="emailAddress"
                                label=" "
                                onChange={onChangeEmailAddress}
                            >
                                {
                                    EMAILADDRESS.map((emailAddress, idx) => {
                                        return <MenuItem key={idx} value={emailAddress}>{emailAddress}</MenuItem>
                                    })
                                }
                            </Select>


                        </Stack>
                        <Button disabled={emailCheck} variant="outlined" startIcon={<SendIcon />} size="small" className="send_btn" onClick={handleClickSend} >
                            
                        </Button>
                    </Stack>
                    <Stack direction="row" alignItems="center" spacing={5}>
                        <TextField
                            disabled={securityCheck}
                            error={incorrectSecureCode}
                            helperText={incorrectSecureCode && ' .'}
                            id='securityCode'
                            type='text'
                            value={inputSecurityCode}
                            label=''
                            variant='outlined'
                            size='small'
                            margin='normal'
                            onChange={onChangeSecurityCode}
                            sx={{ width: '40%' }} s />
                        <Stack direction="row" alignItems="center" style={{ marginTop: 5 }}>
                            <Button disabled={securityCheck} variant="outlined" size="small" onClick={handleClickSecureCodeCheck}>
                                
                            </Button>&nbsp;
                            <Button disabled={securityCheck} variant="outlined" size="small" onClick={handleClickResend}>
                                
                            </Button>
                        </Stack>
                    </Stack>
                    <Stack spacing={1} className="PW_Stack">
                        <TextField
                            disabled={!securityCheck}
                            id='password'
                            error={emptyPW}
                            type='password'
                            helperText={emptyPW && ' .'}
                            value={password}
                            label=''
                            variant='outlined'
                            size='small'
                            margin='normal'
                            onChange={onChangePassword} />
                        <TextField
                            disabled={!securityCheck}
                            id='passwordCheck'
                            error={incorrectPW}
                            type='password'
                            helperText={incorrectPW && ' .'}
                            value={passwordCheck}
                            label=' '
                            variant='outlined'
                            size='small'
                            margin='normal'
                            onChange={onChangePasswordCheck} />
                    </Stack>
                    <Box className="btn_area" style={{ margin: '30px 0 10px 0' }}>
                        <button className="btn" onClick={handleClickChangePassword}>
                            
                        </button>
                    </Box>
                </Box>
            </Modal>
        </div>
    )
}

export default FindPW