import React from 'react';
import { useState } from 'react'
import { Link } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box'
import Stack from '@mui/material/Stack'
import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import FindPW from '../components/FindPW';
import "./css/SignUp.css";



const SignIn = () => {
    const [email, setId] = useState('')
    const [pw, setPw] = useState('')

    const appChangeId = (e) => {
        setId(e.target.value)
    }

    const appChangePw = (e) => {
        setPw(e.target.value)
    }

    const onClickLogin = (e) => {
        e.preventDefault()
        const data = {
            email: email,
            pw: pw,
        }

        fetch("http://localhost:3001/signin", {
            method: 'post',
            headers: {
                "content-type": "application/json",
            },
            body: JSON.stringify(data)
        })
            .then((res) => res.json())
            .then((json) => {
                console.log(json)

                if (json.id === undefined) {
                    // id    userId = undefined, msg = ' id   .'
                    console.log(' id')
                    alert(' ID.')
                } else if (json.id === null) {
                    // id , pw    userId = null , msg = undefined
                    console.log(' ')
                    alert(' .')
                } else if (json.id === email) {
                    // id, pw   userId = userId1, msg = undefined
                    console.log(' ')
                    sessionStorage.setItem('userId', email)
                    console.log(sessionStorage.getItem('userId'))
                    //     ()
                    document.location.href = '/'
                }

            })
            .catch()
    }


    const onClickSignup = () => {
        console.log('  ')
    }

    const onClickFindPassword = () => {
        console.log('   ')
    }

    return (
        <div className="fade-in">
            <Stack direction="row" justifyContent="space-between" className="nav">
                <Box style={{ width: 30 }}></Box>
                <Link to='/'>
                    <Stack className="to_home" direction="row">
                        <img className="to_home_img" alt="YouCanGraduate" src="img/logo.png"></img>
                        <span className="to_home_title">  ?</span>
                    </Stack>
                </Link>
                <Box className="to_mypage">
                    <Link to="/mypage">
                        <AccountCircleRoundedIcon />
                    </Link>
                </Box>
            </Stack>
            <Box className="signin">
                
            </Box>
            <Box className="text_area_in" component="form">
                <TextField
                    className="text_in"
                    name='id'
                    label=''
                    variant="outlined"
                    margin="normal"
                    onChange={appChangeId} />
                <TextField
                    className="text_in"
                    name='pw'
                    label=''
                    type='password'
                    variant="outlined"
                    margin="normal"
                    onChange={appChangePw} />
            </Box>
            <Box className="btn_area">
                <Link to='/'>
                    <button className="btn" onClick={onClickLogin}></button>
                </Link>
            </Box>
            <Box className="sub_btn_area">
                <Link to='/signup'>
                    <button className="sub_btn" onClick={onClickSignup}></button>
                </Link>
                <FindPW></FindPW>
            </Box>
        </div>
    );
};

export default SignIn;