import React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box'
import Stack from '@mui/material/Stack'
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import './css/Result.css';
import Header from '../components/Header';

const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
    '&:before': {
        display: 'none',
    },
}));

const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
        expandIcon={<ExpandMoreIcon />}
        {...props}
    />
))(({ theme }) => ({
    flexDirection: 'row',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
        transform: 'rotate(180deg)',
    },
    '& .MuiAccordionSummary-content': {
        marginLeft: theme.spacing(-2),
        marginTop: theme.spacing(0.5),
        marginBottom: theme.spacing(1),
    },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(1),
    marginTop: theme.spacing(2),
    backgroundColor: '#F6f6f6',
}));

const Result = () => {
    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    return (
        <div className="fade-in">
            <Header mypage />
            <Stack className="result_stack" justifyContent="center" direction="row">
                <span className="r0"></span>
                <span className="r1"></span>
                <span className="r0">!</span>
                {/* <span className="r0"></span>
                <span className="r2"></span>
                <span className="r0">! </span> */}
            </Stack>
            <Box className="result_detail">
                <Box className="detail_box">
                    <div className="stack">
                        <img className="check_img" alt="nope" src="img/nope.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content"><u>5</u> . <b style={{ color: 'crimson' }}>3</b> .</span>
                </Box>
                <Box className="detail_box">
                    <div className="stack">
                        <img className="check_img" alt="yeah" src="img/yeah.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content"><u>850</u> 700 .</span>
                </Box>
                <Box className="detail_box">
                    <Accordion className="acc" expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                        <AccordionSummary aria-controls="panel1d-content">
                            <div>
                                <img className="check_img0" alt="nope" src="img/nope.png"></img>
                                <span className="detail_title2"></span>
                            </div>
                            <span className="detail_content2"> <u>122</u> <b style={{ color: 'crimson' }}>18</b> .</span>
                        </AccordionSummary>
                        <AccordionDetails>
                            <Stack className="category" direction="row" spacing={1}>
                                <img className="check_img2" alt="yeah" src="img/yeah.png"></img>
                                <span className="category_title"></span>
                                <span className="category_content">16 / 16</span>
                            </Stack>
                            <Stack className="category" direction="row" spacing={1}>
                                <img className="check_img2" alt="nope" src="img/nope.png"></img>
                                <span className="category_title"></span>
                                <span className="category_content">2 / 30</span>
                            </Stack>
                            <Stack className="category" direction="row" spacing={1}>
                                <img className="check_img2" alt="nope" src="img/nope.png"></img>
                                <span className="category_title"></span>
                                <span className="category_content">3 / 84</span>
                            </Stack>
                        </AccordionDetails>
                    </Accordion>
                </Box>
                <Box className="detail_box">
                    <div className="stack">
                        <img className="check_img" alt="yeah" src="img/yeah.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content"><u>4.5</u> 2.0 .</span>
                </Box>
                <Box className="detail_box">
                    <div className="stack">
                        <img className="check_img" alt="yeah" src="img/yeah.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content">  .</span>
                </Box>
                <Box className="detail_box">
                    <div className="stack">
                        <img className="check_img" alt="nope" src="img/nope.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content"><u>3</u> . <b style={{ color: 'crimson' }}>1</b> .</span>
                </Box>
                <Box className="final_detail_box">
                    <div className="stack">
                        <img className="check_img" alt="yeah" src="img/yeah.png"></img>
                        <span className="detail_title"></span>
                    </div>
                    <span className="detail_content"> 1,2  .</span>
                </Box>
            </Box>
        </div>
    );
};

export default Result;